body {
  margin: 0;
  padding: 0;
  font-family:'Roboto', cursive, sans-serif, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(
          to left,
          rgba(41, 150, 23, 1) 0%,
          rgba(58, 208, 32, 1) 100%
  );
  text-align: center;
}
.app-title{
  margin-top: 75px;
  margin-bottom: 50px;
  font-size: 90px;
  color: #237C13;
  font-family: 'Bigelow Rules', cursive;
  text-shadow: 5px 5px #1B620F;
}